// pageManager.js - „Éö„Éº„Ç∏ÁÆ°ÁêÜ„Å®„Éä„Éì„Ç≤„Éº„Ç∑„Éß„É≥

import { showNotification } from '../utils/helpers.js';
import onboardingManager from './onboardingManager.js';
import { lazyLoader } from '../utils/lazyLoader.js';

class PageManager {
  constructor() {
    this.currentPage = 'dashboard';
    this.pageCache = new Map();
    this.isInitialized = false;
  }

  /**
   * „Éö„Éº„Ç∏ÁÆ°ÁêÜ„ÇíÂàùÊúüÂåñ
   */
  async initialize() {
    if (this.isInitialized) {
      return;
    }

    this.isInitialized = true;
    console.log('PageManager initialized');
  }

  /**
   * „Éë„Éº„Ç∑„É£„É´„Ç≥„É≥„ÉÜ„É≥„ÉÑ„ÇíË™≠„ÅøËæº„Åø
   * @param {string} partialName - „Éë„Éº„Ç∑„É£„É´Âêç
   * @returns {Promise<string>} HTML„Ç≥„É≥„ÉÜ„É≥„ÉÑ
   */
  async loadPartial(partialName) {
    try {
      if (this.pageCache.has(partialName)) {
        return this.pageCache.get(partialName);
      }

      const response = await fetch(`partials/${partialName}.html`);
      if (!response.ok) {
        throw new Error(`Failed to load ${partialName}`);
      }

      const content = await response.text();
      this.pageCache.set(partialName, content);
      return content;
    } catch (error) {
      console.error(`Error loading partial ${partialName}:`, error);
      return `<div class="text-center text-gray-500 py-8">
                <i class="fas fa-exclamation-triangle text-2xl mb-4"></i>
                <p>„Éö„Éº„Ç∏„ÅÆË™≠„ÅøËæº„Åø„Å´Â§±Êïó„Åó„Åæ„Åó„Åü</p>
            </div>`;
    }
  }

  /**
   * „Éö„Éº„Ç∏„Å´„Éä„Éì„Ç≤„Éº„Éà
   * @param {string} pageName - „Éö„Éº„Ç∏Âêç
   */
  async navigateToPage(pageName) {
    try {
      // „Éä„Éì„Ç≤„Éº„Ç∑„Éß„É≥Áä∂ÊÖã„ÇíÊõ¥Êñ∞
      document.querySelectorAll('.nav-item').forEach((item) => {
        item.classList.remove('active');
      });
      document
        .querySelector(`[data-page="${pageName}"]`)
        ?.classList.add('active');

      // „Éö„Éº„Ç∏„Ç≥„É≥„ÉÜ„É≥„ÉÑ„ÇíË™≠„ÅøËæº„Åø
      const mainContent = document.querySelector('main');
      const partialName = this.getPartialName(pageName);
      const content = await this.loadPartial(partialName);

      // „Ç≥„É≥„ÉÜ„É≥„ÉÑ„ÇíÁΩÆÊèõ
      mainContent.innerHTML = content;

      // ÁèæÂú®„ÅÆ„Éö„Éº„Ç∏„ÇíÊõ¥Êñ∞
      this.currentPage = pageName;

      // „Éö„Éº„Ç∏Âõ∫Êúâ„ÅÆÊ©üËÉΩ„ÇíÂàùÊúüÂåñ
      this.initializePage(pageName);

      console.log(`Navigated to ${pageName} page`);
    } catch (error) {
      console.error('Navigation error:', error);
      showNotification('„Éö„Éº„Ç∏„ÅÆË™≠„ÅøËæº„Åø„Å´Â§±Êïó„Åó„Åæ„Åó„Åü', 'error');
    }
  }

  /**
   * „Éö„Éº„Ç∏Âêç„Åã„Çâ„Éë„Éº„Ç∑„É£„É´Âêç„ÇíÂèñÂæó
   * @param {string} pageName - „Éö„Éº„Ç∏Âêç
   * @returns {string} „Éë„Éº„Ç∑„É£„É´Âêç
   */
  getPartialName(pageName) {
    const partialMap = {
      'exercises-management': 'exercises-management',
      exercises: 'exercises-management',
    };
    return partialMap[pageName] || pageName;
  }

  /**
   * „Éö„Éº„Ç∏Âõ∫Êúâ„ÅÆÊ©üËÉΩ„ÇíÂàùÊúüÂåñ
   * @param {string} pageName - „Éö„Éº„Ç∏Âêç
   */
  initializePage(pageName) {
    // ÂãïÁöÑ„Ç§„É≥„Éù„Éº„Éà„Çí‰ΩøÁî®„Åó„Å¶„Éö„Éº„Ç∏„É¢„Ç∏„É•„Éº„É´„ÇíË™≠„ÅøËæº„Åø
    this.loadPageModule(pageName);
  }

  /**
   * „Éö„Éº„Ç∏„É¢„Ç∏„É•„Éº„É´„ÇíÂãïÁöÑ„Å´Ë™≠„ÅøËæº„ÅøÔºàÈÅÖÂª∂„É≠„Éº„Éá„Ç£„É≥„Ç∞ÂØæÂøúÔºâ
   * @param {string} pageName - „Éö„Éº„Ç∏Âêç
   */
  async loadPageModule(pageName) {
    try {
      console.log(`üîÑ „Éö„Éº„Ç∏„É¢„Ç∏„É•„Éº„É´Ë™≠„ÅøËæº„ÅøÈñãÂßã: ${pageName}`);
      const startTime =
        typeof window !== 'undefined' && window.performance
          ? window.performance.now()
          : Date.now();

      // ÈÅÖÂª∂„É≠„Éº„ÉÄ„Éº„Çí‰ΩøÁî®„Åó„Å¶„É¢„Ç∏„É•„Éº„É´„ÇíË™≠„ÅøËæº„Åø
      const module = await lazyLoader.loadPageModule(pageName);

      if (module.default && typeof module.default.initialize === 'function') {
        await module.default.initialize();
      }

      const loadTime =
        (typeof window !== 'undefined' && window.performance
          ? window.performance.now()
          : Date.now()) - startTime;
      console.log(
        `‚úÖ „Éö„Éº„Ç∏„É¢„Ç∏„É•„Éº„É´Ë™≠„ÅøËæº„ÅøÂÆå‰∫Ü: ${pageName} (${loadTime.toFixed(2)}ms)`
      );

      // ÈÅÖÂª∂„É≠„Éº„ÉâÂØæË±°„ÅÆÁîªÂÉè„Å®„Ç≥„É≥„Éù„Éº„Éç„É≥„Éà„ÇíÁõ£Ë¶ñ
      this.observeLazyElements();
    } catch (error) {
      console.warn(
        `Page module for ${pageName} not found or failed to load:`,
        error
      );
      // „Éï„Ç©„Éº„É´„Éê„ÉÉ„ÇØÂá¶ÁêÜ
      this.initializePageFallback(pageName);
    }
  }

  /**
   * ÈÅÖÂª∂„É≠„Éº„ÉâÂØæË±°„ÅÆË¶ÅÁ¥†„ÇíÁõ£Ë¶ñ
   */
  observeLazyElements() {
    // ÈÅÖÂª∂„É≠„Éº„ÉâÂØæË±°„ÅÆÁîªÂÉè„ÇíÁõ£Ë¶ñ
    const lazyImages = document.querySelectorAll(
      'img[data-src], img[loading="lazy"]'
    );
    lazyImages.forEach((img) => {
      lazyLoader.observeImage(img);
    });

    // ÈÅÖÂª∂„É≠„Éº„ÉâÂØæË±°„ÅÆ„Ç≥„É≥„Éù„Éº„Éç„É≥„Éà„ÇíÁõ£Ë¶ñ
    const lazyComponents = document.querySelectorAll('[data-lazy-component]');
    lazyComponents.forEach((element) => {
      const componentType = element.dataset.lazyComponent;
      lazyLoader.observeComponent(element, componentType);
    });
  }

  /**
   * „Éö„Éº„Ç∏ÂàùÊúüÂåñ„ÅÆ„Éï„Ç©„Éº„É´„Éê„ÉÉ„ÇØÂá¶ÁêÜ
   * @param {string} pageName - „Éö„Éº„Ç∏Âêç
   */
  initializePageFallback(pageName) {
    console.log(`Initializing ${pageName} page with fallback`);

    switch (pageName) {
      case 'dashboard':
        this.initializeDashboard();
        break;
      case 'workout':
        this.initializeWorkout();
        break;
      case 'calendar':
        this.initializeCalendar();
        break;
      case 'analytics':
        this.initializeAnalytics();
        break;
      case 'progress':
        this.initializeProgress();
        break;
      case 'exercises':
        this.initializeExercises();
        break;
      case 'settings':
        this.initializeSettings();
        break;
      case 'help':
        this.initializeHelp();
        break;
      case 'privacy':
        this.initializePrivacy();
        break;
      default:
        console.log(`No specific initialization for ${pageName}`);
    }
  }

  /**
   * „ÉÄ„ÉÉ„Ç∑„É•„Éú„Éº„Éâ„Éö„Éº„Ç∏„ÅÆÂàùÊúüÂåñ
   */
  initializeDashboard() {
    console.log('Dashboard page initialized');

    // „Ç™„É≥„Éú„Éº„Éá„Ç£„É≥„Ç∞„ÉÅ„Çß„ÉÉ„ÇØÔºà„ÉÄ„ÉÉ„Ç∑„É•„Éú„Éº„ÉâÂàùÊúüÂåñÊôÇÔºâ
    if (onboardingManager.isOnboardingNeeded()) {
      console.log('„Ç™„É≥„Éú„Éº„Éá„Ç£„É≥„Ç∞„ÅåÂøÖË¶Å„Åß„Åô');
      setTimeout(() => {
        onboardingManager.startOnboarding();
      }, 1000); // 1ÁßíÂæå„Å´ÈñãÂßãÔºàUI„ÅÆÂàùÊúüÂåñ„ÇíÂæÖ„Å§Ôºâ
    }

    // Á≠ãËÇâÈÉ®‰Ωç„ÇØ„É™„ÉÉ„ÇØ„Éè„É≥„Éâ„É©„Éº
    document.querySelectorAll('.muscle-part').forEach((part) => {
      part.addEventListener('click', () => {
        const muscle = part.dataset.muscle;
        console.log(`Clicked muscle part: ${muscle}`);
      });
    });
  }

  /**
   * „ÉØ„Éº„ÇØ„Ç¢„Ç¶„Éà„Éö„Éº„Ç∏„ÅÆÂàùÊúüÂåñ
   */
  initializeWorkout() {
    console.log('Workout page initialized');
  }

  /**
   * „Ç´„É¨„É≥„ÉÄ„Éº„Éö„Éº„Ç∏„ÅÆÂàùÊúüÂåñ
   */
  initializeCalendar() {
    console.log('Calendar page initialized');
  }

  /**
   * ÂàÜÊûê„Éö„Éº„Ç∏„ÅÆÂàùÊúüÂåñ
   */
  initializeAnalytics() {
    console.log('Analytics page initialized');
  }

  /**
   * „Éó„É≠„Ç∞„É¨„ÉÉ„Ç∑„Éñ„Éª„Ç™„Éº„Éê„Éº„É≠„Éº„Éâ„Éö„Éº„Ç∏„ÅÆÂàùÊúüÂåñ
   */
  initializeProgress() {
    console.log('Progress page initialized');
  }

  /**
   * „Ç®„ÇØ„Çµ„Çµ„Ç§„Ç∫„Éö„Éº„Ç∏„ÅÆÂàùÊúüÂåñ
   */
  initializeExercises() {
    console.log('Exercises page initialized');
  }

  /**
   * Ë®≠ÂÆö„Éö„Éº„Ç∏„ÅÆÂàùÊúüÂåñ
   */
  initializeSettings() {
    console.log('Settings page initialized');
  }

  /**
   * „Éò„É´„Éó„Éö„Éº„Ç∏„ÅÆÂàùÊúüÂåñ
   */
  initializeHelp() {
    console.log('Help page initialized');
  }

  /**
   * „Éó„É©„Ç§„Éê„Ç∑„Éº„Éö„Éº„Ç∏„ÅÆÂàùÊúüÂåñ
   */
  initializePrivacy() {
    console.log('Privacy policy page initialized');
  }

  /**
   * „Éä„Éì„Ç≤„Éº„Ç∑„Éß„É≥„ÇíÂàùÊúüÂåñ
   */
  initializeNavigation() {
    document.querySelectorAll('.nav-item').forEach((item) => {
      item.addEventListener('click', async (e) => {
        e.preventDefault();
        const page = item.dataset.page;
        if (page && page !== this.currentPage) {
          await this.navigateToPage(page);
        }
      });
    });
  }

  /**
   * „Éò„ÉÉ„ÉÄ„Éº„ÇíË™≠„ÅøËæº„Åø
   */
  async loadHeader() {
    try {
      const headerContainer = document.getElementById('header-container');
      if (headerContainer) {
        const headerContent = await this.loadPartial('header');
        headerContainer.innerHTML = headerContent;
      }
    } catch (error) {
      console.error('Error loading header:', error);
    }
  }

  /**
   * „Çµ„Ç§„Éâ„Éê„Éº„ÇíË™≠„ÅøËæº„Åø
   */
  async loadSidebar() {
    try {
      const sidebarContainer = document.getElementById('sidebar-container');
      if (sidebarContainer) {
        const sidebarContent = await this.loadPartial('sidebar');
        sidebarContainer.innerHTML = sidebarContent;
      }
    } catch (error) {
      console.error('Error loading sidebar:', error);
    }
  }

  /**
   * ÁèæÂú®„ÅÆ„Éö„Éº„Ç∏„ÇíÂèñÂæó
   * @returns {string} ÁèæÂú®„ÅÆ„Éö„Éº„Ç∏Âêç
   */
  getCurrentPage() {
    return this.currentPage;
  }
}

// „Ç∑„É≥„Ç∞„É´„Éà„É≥„Ç§„É≥„Çπ„Çø„É≥„Çπ„Çí„Ç®„ÇØ„Çπ„Éù„Éº„Éà
export const pageManager = new PageManager();
